#mentoring{
    bottom: 0;
    z-index: 10;
    background: #FEFEE4;
    .inputBox{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        min-height: 49px;
        background: #fff;
        border-top:0.5px solid #ddd;
        .iconfont{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            font-size:30px;
        }
        .icon-yuyin{left: 5px;color: #999;}
        .icon-Ovalx{
            right: 5px;
            color: #afafaf;
        }
        .text{
            height: 100%;
        }
        textarea{
            position: absolute;
            width: 75%;
            height: 90%;
            line-height: 30px;
            top: 50%;
            color: #000000;
            left: 50%;
            transform: translate(-50%,-50%);
            bottom: 10px;
            padding:0 10px ;
            max-height: 100px;
        }
        .textshow{
            word-break: break-all;
            line-height: 30px;
            padding: 0 10px;
            max-height: 100px;
            color: transparent;
            width: 75%;
            margin: auto;
            border-radius: 5px;
        }
    }
    
    .messageBox{
        .doubt{
            background: #fff;
            box-shadow: 5px 5px 5px rgba($color: #000000, $alpha: 0.5);
            margin:10px auto;
            padding:0 10px;
            border-radius: 5px;
            position: relative;
            box-sizing: border-box;
            width: 80%;
            img{
                position: absolute;
                right: 10px;
                top: 10px;
                height: 80%;
                width: 50%;
            }
            p{
                padding: 10px;
                font-size: 16px;
            }
        }
        li{
            padding:5px 10px;
            position: relative;
            .nickname{
                position: absolute;
                font-size: 14px;
            }
            img{
                position: absolute;
                border-radius: 50%;
                width: 40px;
                height: 40px;
                top: 5px;
                display: inline-block;
            }
            &:nth-of-type(2n+1){
                img{left: 5px;}
                .nickname{top:2px;left: 60px;}
                text-align: left;
                .text{ 
                    margin-left: 40px;
                    background: rgba($color: #0871fa, $alpha: 0.7);
                }
            }
            &:nth-of-type(2n){
                img{right: 5px;}
                .nickname{top:2px;right: 60px;}
                text-align: right;
                .text{ 
                    margin-right: 40px;
                    background: rgba($color: #fac108, $alpha: 0.7);
                }
            }
            .text{
                color: #fff;
                display: inline-block;
                margin-top: 20px;
                text-align: left;
                max-width: 88%;
                padding:5px 10px;
                font-size:16px;
                border-radius: 30px;
            }
        }
    }
    .overlay {
		pointer-events: none;
	}
}